.w3-table tr td {
	vertical-align: middle !important;
}

.navbar {
	border: 0 !important;
	margin-bottom: 0 !important;
}

.top {
	position: fixed;
	width: 100%;
	z-index: 1;
}

.topnavL2, .topnavL3 {
	width: 100%;
}

.topnavL2 a, .topnavL3 a {
	padding:4px 6px 4px 6px !important;
	border-bottom:none !important;
	display:inline-block;
}

.topnavL2 a:hover, .topnavL3 a:hover {
	border-bottom:none !important;
	text-decoration: none !important;
}

.topnavL2 {
	line-height: 1.0;
}

.topnavL3 {
	line-height: .8;
}

.dropdown-menu {
	padding: 0 !important;
}

.panel-heading {
	font-size: 20px;
}

.footer {
	height: 30px;
	font-size: 12px;
}

.footer .row {
	padding-top: 6px;
}

.navbar-fixed-top .navbar-collapse {
	padding-right: 15px;
	padding-left: 15px;
}

.underline {
	text-decoration: underline;
}

@media (max-width:601px) {
	.topnavsub {
		display:none !important
	}
}

font-awesome {
	font-family: 'FontAwesome', Arial;
}

.search-div {
	height: 34px;
}

.search {
	height: 28px;
	padding-left: 8px;
}

#search-icon {
  position: relative;
  z-index: 1;
  margin-left: -35px;
  top: 1px;
}
